<template>
  <div>
    <div class="navbar">
      <nav>
        <v-app-bar
          app
          color="rgba(15, 15, 15, 0.78)"
        >
          <div class="d-flex align-center">
            <v-toolbar-title class="grey--text" @click="goToTop">
              <span class="title">My Portfolio</span>
            </v-toolbar-title>
          </div>

          <v-spacer></v-spacer>

          <ul>
            <li class="item" @click="goToPortFolio">My Project</li>
            <li class="item" @click="goToContactMe">Contact Me</li>
          </ul>
          <!-- <router-link class="item" to="/about">About</router-link> -->
        </v-app-bar>
      </nav>
    </div>
    <div class="introduction-wrapper">
      <Introduction v-bind:scrollDownClick="goToPortFolio"/>
      <div ref="PortFolioComponent">
        <PortFolio/>
      </div>
      <div ref="ContactMeComponent">
        <ContactForm/>
      </div>
    </div>
  </div>
</template>

<style lang="scss">
  nav {
    .title{
      color: white;
      cursor: pointer;
    }

    ul {
      list-style: none;
    }

    ul li{
      display: inline-block;
    }

    li.item{
      color: white;
      text-decoration: none;
      padding: 10px;
      cursor: pointer;
    }
  }

  .navbar nav ul{
    padding: 0;
  }
</style>
<script>
import Introduction from '@/components/Home/Introduction.vue';
import PortFolio from '@/components/Home/PortFolio.vue';
import ContactForm from '@/components/Home/ContactForm.vue';

export default {
  name: 'Home',
  components: {
    Introduction,
    PortFolio,
    ContactForm,
  },
  methods: {
    goToTop() {
      window.scrollTo({
        top: 0,
        behavior: 'smooth',
      });
    },
    goToPortFolio() {
      const PortFolioCoordinateY = this.$refs.PortFolioComponent.getBoundingClientRect().y;
      try {
        this.$refs.PortFolioComponent.scrollIntoView({
          behavior: 'smooth',
        });
      } catch (error) {
        window.scrollTo(0, PortFolioCoordinateY);
      }
    },
    goToContactMe() {
      const ContactMeCoordinateY = this.$refs.ContactMeComponent.getBoundingClientRect().y;
      try {
        this.$refs.ContactMeComponent.scrollIntoView({
          behavior: 'smooth',
        });
      } catch (error) {
        window.scrollTo(0, ContactMeCoordinateY);
      }
      // window.scrollTo({
      //   y: ContactMeCoordinateY,
      //   behavior: 'smooth',
      // });
    },
  },
};
</script>
